.children-courses-container{
    padding-bottom: 160px;
}

.children-courses-container > .subtitle{
    margin: 32px 0;
    text-align: left;
}

.children-courses-card-list{
    display: flex;
    flex-wrap: wrap;

    @media screen and (min-width: 768px) {
    gap: 20px;
    }
    @media screen and (min-width: 1200px) {
        gap: 32px;
    }
}

.children-courses-card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 9px 9px 0 0;
    position: relative;
    cursor: pointer;
    transition: all 280ms cubic-bezier(.4,0,.2,1);

    @media screen and (min-width: 768px) {
        flex-basis: calc((100% - 20px) / 2); 
        } 

    @media screen and (min-width: 1200px) {
        flex-basis: calc((100% - 64px) / 3); 
        } 
}

.children-courses-card:hover{
    box-shadow: 0 0 12px;
    scale: 1.01;
}


.additional-course-info-block{
    position: absolute;
    top: 16px;
    right: 16px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.additional-course-info-block > img{
    width: 22px;
    @media screen and (min-width: 768px) {
        width: 32px; 
        } 
}

.additional-course-info-block > 
img:not(:last-child){
margin-bottom: 8px; 
}

.children-courses-card:nth-child(3n + 1) {
    background-color: #6BBC60;
  }

  .children-courses-card:nth-child(3n + 2) {
    background-color: #FFB539;
  }

  .children-courses-card:nth-child(3n + 3) {
    background-color: #E0B2C9;
  }

.course-card-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px 0 20px;
    border-radius: 9px 9px 0 0;
    background-color:  #F4FFF6;
    box-shadow: 1.6px 4.7px 11px 0px rgba(153, 175, 153, 0.25);

    @media screen and (min-width: 768px) {
        padding: 10px 14px 20px; 
        } 

}

.course-subinfo{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    padding-bottom: 10px;
    margin-bottom: 16px;
    border-bottom: 1px solid #6BBC60;
}

.course-subinfo >img{
    width: 28px;
    margin-right: 4px;
    @media screen and (min-width: 768px) {
        width: 32px;
        margin-right: 8px; 
        } 
}

.course-name{
    color: #0B221C;
    font-family: Nunito Sans;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.course-card-text-box:not(:last-child){
    margin-right: 8px;
}

.course-card-text{
    color:  #0B221C;
font-family: Nunito Sans;
font-size: 9px;
font-style: normal;
font-weight: 600;
line-height: normal;

@media screen and (min-width: 768px) {
    font-size: 11px; 
    } 

}