* {
  box-sizing: border-box;
}

body {
  color: #fff;
  box-sizing: border-box;
  background-color: #fff;
  margin: 0;
  font-family: Nunito Sans, Roboto, Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
  display: block;
}

p, h1, h2, h3, ul {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  border: none;
  margin: 0;
  padding: 0;
}

ul {
  padding: 0;
  list-style: none;
}

.section {
  width: 100%;
}

button {
  cursor: pointer;
}

.main-title {
  color: #000;
  text-align: center;
  margin: 40px 0;
}

.container {
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

@media screen and (min-width: 1270px) {
  .container {
    max-width: 1270px;
  }
}

.header {
  background-color: #f4fff6;
  padding-top: 9px;
  padding-bottom: 9px;
}

.header > .container {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header > .container {
    justify-content: center;
  }
}

.mob-menu {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: #f4fff6;
  flex-direction: column;
  align-items: center;
  padding: 48px 15px 48px 40px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.mob-menu.is-hidden {
  display: none;
}

.mob-menu-closeBtn {
  background-color: inherit;
  border: none;
  position: absolute;
  top: 24px;
  right: 24px;
}

.mob-menu_list {
  flex-direction: column;
  align-items: center;
  margin-bottom: 32px;
  display: flex;
}

.mob-menu_item {
  padding: 8px;
}

.mob-menu_item:not(:last-child) {
  margin-bottom: 12px;
}

.mob-menu-link {
  text-transform: uppercase;
  color: #0b221c;
  font-size: 18px;
  font-weight: 700;
  scale: 1;
}

.mob-menu-link:hover {
  scale: 1.1;
}

.nav {
  margin-left: auto;
  display: none;
}

@media screen and (min-width: 768px) {
  .nav {
    display: block;
  }
}

.nav-list {
  display: flex;
}

.logo-img {
  width: 100%;
}

@media screen and (min-width: 768px) {
  .logo-img {
    width: 68px;
    height: 60px;
  }
}

.nav-item + .nav-item {
  margin-left: 24px;
}

@media screen and (min-width: 1200px) {
  .nav-item + .nav-item {
    margin-left: 48px;
  }
}

.nav-item {
  padding-top: 12px;
  padding-bottom: 12px;
}

.nav-link {
  color: #0b221c;
  font-family: Nunito Sans;
  font-size: 12px;
  font-weight: 400;
}

.lang-select {
  color: #0b221c;
  border: none;
  font-size: 16px;
  font-weight: 500;
}

@media screen and (min-width: 768px) {
  .lang-select {
    margin-left: 24px;
  }
}

@media screen and (min-width: 1200px) {
  .lang-select {
    margin-left: 42px;
    font-size: 16px;
  }
}

.phone-box {
  display: none;
}

@media screen and (min-width: 768px) {
  .phone-box {
    flex-direction: column;
    align-items: center;
    margin-left: 32px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .phone-box {
    margin-left: 60px;
  }
}

.callback-btn {
  width: 148px;
  height: 28px;
  text-transform: uppercase;
  color: #fff;
  background: #0b221c;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-bottom: 12px;
  font-family: Nunito Sans;
  font-size: 10px;
  font-weight: 700;
  display: flex;
  box-shadow: 4px 4px 4px rgba(0, 0, 0, .25);
}

@media screen and (min-width: 1200px) {
  .callback-btn {
    width: 180px;
  }
}

.phone-link {
  color: #0b221c;
  font-family: Nunito Sans;
  font-size: 12px;
}

@media screen and (min-width: 1200px) {
  .phone-link {
    font-size: 14px;
  }
}

.mob-menu-phone-link {
  color: #0b221c;
  margin: 0 auto;
  font-family: Nunito Sans;
  font-size: 16px;
}

.mob-menu__telbox {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.mob-menu__telbox > .callback-btn {
  width: 180px;
  margin-bottom: 16px;
  padding: 16px;
  font-size: 12px;
}

.nav-menu-btn {
  background-color: inherit;
  border: none;
}

@media screen and (min-width: 768px) {
  .nav-menu-btn {
    display: none;
  }
}

.hero {
  width: 100%;
  background-image: url("bg.0d9cd2af.png"), linear-gradient(#6bbc60 0%, #56a04c 44.6%, #55a04a 61.84%, #6bbc60 100%);
  background-position: center;
  background-repeat: no-repeat;
}

.hero > .container {
  flex-direction: column;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .hero > .container {
    align-items: normal;
    padding-top: 80px;
    padding-bottom: 100px;
    display: block;
    position: relative;
  }
}

@media screen and (min-width: 1200px) {
  .hero > .container {
    padding-top: 128px;
    padding-bottom: 166px;
  }
}

.hero-content-box {
  max-width: 307px;
  margin-bottom: 28px;
}

@media screen and (min-width: 768px) {
  .hero-content-box {
    margin-bottom: 0;
    position: relative;
  }
}

@media screen and (min-width: 1200px) {
  .hero-content-box {
    max-width: 450px;
  }
}

.hero-text {
  text-shadow: 3px 3px 6px rgba(21, 20, 8, .25);
  margin-bottom: 36px;
  font-family: Nunito Sans;
  font-size: 18px;
}

@media screen and (min-width: 1200px) {
  .hero-text {
    font-size: 20px;
  }
}

.title {
  text-shadow: 0 4px 4px rgba(0, 0, 0, .25);
  margin-bottom: 28px;
  font-family: Nunito Sans;
  font-size: 32px;
  font-weight: 800;
}

@media screen and (min-width: 1200px) {
  .title {
    max-width: 300px;
    font-size: 48px;
  }
}

.btn {
  max-width: 285px;
  max-height: 48px;
  text-transform: uppercase;
  color: #0b221c;
  background-color: #ffb539;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 14px 72px;
  font-family: Nunito Sans;
  font-size: 14px;
  font-weight: 700;
  display: flex;
  box-shadow: 7px 7px 6px rgba(98, 90, 20, .25);
}

.hero-img {
  width: 360px;
}

@media screen and (min-width: 768px) {
  .hero-img {
    width: 470px;
    position: absolute;
    bottom: 0;
    right: 1%;
  }
}

@media screen and (min-width: 1200px) {
  .hero-img {
    width: 630px;
  }
}

.subtitle {
  color: #0b221c;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 32px;
  font-family: Nunito Sans;
  font-size: 28px;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .subtitle {
    margin-bottom: 64px;
    font-size: 40px;
  }
}

.courses {
  width: 100%;
  background-image: url("courses-bg.5fb4f29b.png");
  background-position: left 0 bottom 0, 0 100%;
  background-repeat: no-repeat;
  padding-top: 36px;
  padding-bottom: 72px;
}

@media screen and (min-width: 768px) {
  .courses {
    padding-top: 48px;
    padding-bottom: 96px;
  }
}

@media screen and (min-width: 1200px) {
  .courses {
    margin-bottom: -40px;
    padding-top: 80px;
    padding-bottom: 174px;
  }
}

.courses > .container {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.courses-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 48px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .courses-list {
    justify-content: space-between;
    gap: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .courses-list {
    justify-content: space-between;
  }
}

.courses-item {
  justify-content: center;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .courses-item {
    flex-basis: calc(50% - 10px);
  }
}

@media screen and (min-width: 1200px) {
  .courses-item {
    flex-basis: calc(25% - 15px);
  }
}

@media screen and (min-width: 768px) {
  .courses-item:nth-child(n+3) {
    margin-top: 28px;
  }
}

@media screen and (min-width: 1200px) {
  .courses-item:nth-child(n+3) {
    margin-top: 0;
  }
}

.courses-circle {
  width: 222px;
  height: 222px;
  background-color: #6bbc60;
  border-radius: 50%;
  position: relative;
}

.courses-category {
  width: 100%;
  color: #0b221c;
  text-align: center;
  color: #0b221c;
  background-color: #f4fff6;
  border-radius: 6px;
  padding: 8px 0;
  font-family: Nunito Sans;
  font-size: 24px;
  position: absolute;
  bottom: -16px;
  left: 0;
  box-shadow: 2px 5px 11px rgba(153, 175, 153, .25);
}

.about {
  background-image: url("about-bg.c063d468.png");
  background-position: right 0 top 0, 100% 0;
  background-repeat: no-repeat;
  padding-bottom: 80px;
}

@media screen and (min-width: 1200px) {
  .about {
    padding-bottom: 160px;
  }
}

.about > .container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .about > .container {
    flex-direction: row;
  }
}

@media screen and (min-width: 1200px) {
  .about > .container {
    align-items: none;
  }
}

.about > .subtitle {
  text-align: center;
}

.block-title {
  color: #0b221c;
  text-align: center;
  margin-bottom: 32px;
  font-family: Nunito Sans;
  font-size: 32px;
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .block-title {
    text-align-last: left;
  }
}

.about-text {
  color: #0b221c;
  font-family: Nunito Sans;
  font-size: 20px;
}

.about-text + .about-text {
  margin-top: 22px;
  margin-bottom: 32px;
}

.img-description {
  color: #0b221c;
  font-family: Nunito Sans;
  font-size: 24px;
  font-weight: 600;
}

@media screen and (min-width: 1200px) {
  .img-description {
    font-size: 32px;
  }
}

@media screen and (min-width: 768px) {
  .img-description > span {
    font-size: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .img-description > span {
    font-size: 64px;
  }
}

.about-img-box {
  position: relative;
}

.about-img-box > .first {
  position: absolute;
  bottom: 24%;
  right: 20px;
}

@media screen and (min-width: 768px) {
  .about-img-box > .first {
    right: -16px;
  }
}

@media screen and (min-width: 1200px) {
  .about-img-box > .first {
    bottom: 18%;
    right: -64px;
  }
}

.about-img-box > .second {
  position: absolute;
  bottom: -20px;
  left: 32%;
}

@media screen and (min-width: 768px) {
  .about-img-box > .second {
    bottom: -32px;
  }
}

@media screen and (min-width: 1200px) {
  .about-img-box > .second {
    bottom: -60px;
  }
}

.post-about {
  width: 100%;
  background-image: linear-gradient(#6bbc60 0%, #56a04c 44.6%, #55a04a 61.84%, #6bbc60 100%);
  padding-top: 60px;
  padding-bottom: 60px;
}

.post-about > .container {
  display: flex;
}

.post-about-list {
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .post-about-list {
    flex-flow: wrap;
    gap: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .post-about-list {
    width: 100%;
    flex-wrap: nowrap;
    gap: 129px;
  }
}

.post-about-item {
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 768px) {
  .post-about-item {
    flex-basis: calc(50% - 16px);
    justify-content: center;
    align-items: center;
  }
}

@media screen and (min-width: 1200px) {
  .post-about-item {
    width: auto;
    flex-basis: calc(25% - 96.75px);
  }
}

.post-about-item + .post-about-item {
  margin-top: 60px;
}

@media screen and (min-width: 768px) {
  .post-about-item + .post-about-item {
    margin-top: 0;
  }
}

.post-about-img {
  margin-bottom: 18px;
}

.postabout-text {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.6;
}

@media screen and (min-width: 1200px) {
  .postabout-text {
    font-size: 23px;
  }
}

.certificate-reviews {
  background-image: url("certificate-bg.b445abb3.png");
  background-position: right -60px top -135px, 100% 0;
  background-repeat: no-repeat;
  position: relative;
}

.certificate {
  padding-top: 60px;
}

@media screen and (min-width: 768px) {
  .certificate {
    padding-top: 80px;
  }
}

@media screen and (min-width: 1200px) {
  .certificate {
    padding-top: 160px;
  }
}

.certificate > .subtitle {
  text-align: center;
}

.certificate-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 32px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .certificate-list {
    gap: 20px 28px;
  }
}

@media screen and (min-width: 1200px) {
  .certificate-list {
    gap: 12px 28px;
  }
}

.certificate-item {
  justify-content: center;
  align-items: center;
