}

.reviews {
  padding-top: 60px;
  padding-bottom: 80px;
}

@media screen and (min-width: 768px) {
  .reviews {
    padding-top: 80px;
    padding-bottom: 100px;
  }
}

@media screen and (min-width: 1200px) {
  .reviews {
    padding-top: 160px;
    padding-bottom: 160px;
  }
}

.reviews > .subtitle {
  text-align: center;
}

@media screen and (min-width: 768px) {
  .reviews > .subtitle {
    margin-bottom: 48px;
  }
}

@media screen and (min-width: 1200px) {
  .reviews > .subtitle {
    margin-bottom: 64px;
  }
}

.reviews:after {
  content: " ";
  width: 100%;
  height: 206px;
  background-color: #f4fff6;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

@media screen and (min-width: 768px) {
  .reviews:after {
