.reviews > .container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.carousel {
  z-index: 5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  bottom: -20px;
}

.carousel-inner {
  justify-content: center;
