.content {
  width: 100%;
  max-width: 974px;
  z-index: 10;
  margin: auto;
  padding: 30px;
}

.slider > button {
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .35s;
  display: flex;
}

@media screen and (min-width: 768px) {
  .slider > button {
    width: 40px;
    height: 40px;
  }
}

.slider > .slick-arrow:before {
  content: " ";
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}

.slider > .slick-prev:before {
  background-image: url("left-arr.a0a5c813.png");
}

.slider > .slick-next:before {
  background-image: url("right-arr.9bf2dc2f.png");
}

.slider > .slick-arrow:hover, .slider > .slick-arrow:focus {
  background-color: #387630;
}

.slider > .slick-prev {
  left: -35px;
}

@media screen and (min-width: 768px) {
  .slider > .slick-prev {
    left: -45px;
  }
}

.slider > .slick-next {
  right: -35px;
}

@media screen and (min-width: 768px) {
  .slider > .slick-next {
    right: -45px;
  }
}

.slider > .slick-dots {
  bottom: -32px;
}

ul.slick-dots li.slick-active button:before {
  opacity: 1;
  color: #6bbc60;
}

